<template>
  <div class="api"
       v-if="auth">
    <div class="name">{{auth.name}}</div>
    <div class="desc">{{auth.desc}}</div>
    <div class="auth__type">
      <div class="group__title">{{$t('auth_type')}}</div>
      <div>{{auth.auth_type}}</div>
    </div>
    <div class="auth__place">
      <div class="group__title">{{$t('auth_place')}}</div>
      <div>{{auth.auth_place}}</div>
    </div>
    <div class="group"
         v-if="auth.groups">
      <div class="group__title">{{$t('groups')}}</div>
      <div class="group__list">
        <div v-for="group in auth.groups"
             :key="group.name">
          <div class="group__item">
            <div class="group__header">
              <i class="el-icon-user-solid icon" />
              <span class="group__header__name">{{group.name}}</span>
              <div class="group__header__desc">{{group.desc}}</div>
            </div>
            <div class="group__body">
              <div class="group__perm">
                <div class="group__info__title">
                  {{$t('has_perm')}}
                </div>
                <div class="group__perm__list">
                  <div class="group__perm__item"
                       v-for="(methods,url) in group.has_perms"
                       :key="url">
                    <code class="group__perm__url">
                      {{url}} ：
                    </code>
                    <span class="group__perm__method"
                          v-for="method in getMethod(methods)"
                          :key="method[0]">
                      <Method :methods="method"
                              size="small" />
                    </span>
                  </div>
                </div>
              </div>
              <div class="group__perm">
                <div class="group__info__title">
                  {{$t('no_perm')}}
                </div>
                <div class="group__perm__list">
                  <div class="group__perm__item"
                       v-for="(methods,url) in group.no_perms"
                       :key="url">
                    <code class="group__perm__url">
                      {{url}} ：
                    </code>
                    <span class="group__perm__method"
                          v-for="method in getMethod(methods)"
                          :key="method[0]">
                      <Method :methods="method"
                              size="small" />
                    </span>
                  </div>
                </div>
              </div>
              <div class="group__response">
                <div class="group__info__title">
                  {{$t('no_perm_response')}}
                </div>
                <Code :json="group.no_perm_response"></Code>
              </div>
              <div class="group__users">
                <div class="group__info__title">
                  {{$t('test_users')}} ({{Object.keys(group.users).length}})
                </div>
                <el-row class="group__users__list"
                        :gutter="10">
                  <el-col :md="12"
                          :sm="24"
                          v-for="(user,key) in group.users"
                          :key="key">
                    <Code :json="user">
                    </Code>
                  </el-col>
                </el-row>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Row, Col } from 'element-ui';
import Method from '../../components/API/Method.vue';
import Code from '../../components/Code.vue';
import { defaultMethods } from '../../api';

export default {
  components: {
    'el-row': Row,
    'el-col': Col,
    Method,
    Code,
  },
  computed: {
    auth() {
      return this.$store.state.auth;
    },
  },
  methods: {
    getMethod(method) {
      if (method.includes('*')) {
        return defaultMethods.map(v => [v]);
      }
      return method.map(v => [v]);
    },
  },
};
</script>

